      :background="background"
      layout="total, sizes, prev, pager, next"
      :total="theCountItemsTotal"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>

<script>
export default {
  name: "settings-projects-pager",

  data() {
    return {
      background: "#159951",
      theCountItemsTotal: 0,
      pageSize: 1,
      currentPage: 1,
    };
  },
